/**
 * Clear inner floats
 */
 .clearfix{
  z-index: -1;
}
.clearfix::after {
  clear: both;
  content: '';
  display: table;
}

/**
 * Hide text while making it readable for screen readers
 * 1. Needed in WebKit-based browsers because of an implementation bug;
 *    See: https://code.google.com/p/chromium/issues/detail?id=457146
 */
.gs-hide-text {
  overflow: hidden;
  padding: 0; /* 1 */
  text-indent: 101%;
  white-space: nowrap;
}

/**
 * Hide element while making it readable for screen readers
 * Shamelessly borrowed from HTML5Boilerplate:
 * https://github.com/h5bp/html5-boilerplate/blob/master/src/css/main.css#L119-L133
 */
.gs-visually-hidden { 
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.gs-hidden{
  visibility: hidden;
  opacity:0;
}

.gs-visible{
  visibility: visible;
  opacity:1;
}

.gs-full-content{
  width: 100%;
  height: 100%;
}

.gs-list{
  ligs-style: none; 
  ligs-style-image: none; 
  margin: 0;
  padding: 0; 
}

.gs-vert-center{
  top:50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  transform: translateY(-50%);
}

.gs-overflown{
  overflow: hidden;
}

